/**
 * @authors liming.liu (liming.liu@71bird.com)
 * @date    2018-09-03 19:59:38
 */
<template>
  <div class="ranklist scroller">
<!--     <div class="tabbar">
      <div class="bar">今日幸运榜</div>
      <div class="bar">历史幸运榜</div>
      <div class="bar">金主排行榜</div>
    </div> -->
    <div class="ranktitle">
      <div class="titletxt">
        <h4>游戏历史</h4>
      </div>
      <div class="justme">
        <a href="javascript:;" :class="{active: onlyMe}" @click="getMyEvent">我的记录</a>
      </div>
    </div>
    <div class="ranklistcontent">
      <ul>
        <li class="title">
          <div class="ttitle">
            <div class="player">玩家</div>
            <div class="bets">赌注</div>
            <div class="type">下注</div>
            <div class="res">结果</div>
            <div class="winning">奖金</div>
            <div class="jackpot">幸运奖</div>
          </div>
        </li>
        <li v-for="(item, index) in historyList" :key="item.transactionHash" @click="openRecord(item)" v-if="historyList.length > 0">
          <div class="ttitle">
            <div class="player">
              <div class="avater">
                <img :src="`/static/images/player${index%3}.svg`" alt="">
              </div>
              <div class="addr">
                <span v-text="item.returnValues.PlayerAddress"></span>
              </div>
            </div>
            <div class="bets" v-text="item.returnValues.BetValue">0.01</div>
            <div class="type">
              <span v-text="'≤' + item.returnValues.PlayerNumber"></span>
              <!-- <img src="/static/images/5.svg" alt=""> -->
            </div>
            <div class="res">
              <!-- <img src="/static/images/win.svg" alt=""> -->
              <span v-text="item.returnValues.DiceResult"></span>
            </div>
            <div class="winning" v-text="item.returnValues.Status === '1' ? item.returnValues.ProfitValue : '-'">-</div>
            <div class="jackpot">0</div>
            <div class="arrow"><button :class="{active: item.active}"></button></div>
          </div>
          <div class="tcontent" v-if="item.active">
            <tx-content :content="item" :type='type'></tx-content>
          </div>
        </li>
        <li v-if="historyList.length === 0" class="loading">
          <img src="/static/images/loading.svg" alt="">
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import TxContent from './TxContent'
import {mapActions} from 'vuex'
export default {
  name: 'ranklist',
  props: ['contract', 'type'],
  data () {
    return {
      myAddr: '', // 我的地址，用作筛选
      onlyMe: false,
      historyList: [],
      pendingMap: {}
    }
  },
  created () {
    this.initEvent()
    if (typeof this.$mweb3.version === 'object') {
      this.myAddr = this.$mweb3.eth.defaultAccount
    }
  },
  components: {
    TxContent
  },
  methods: {
    ...mapActions(['changePending']),
    initEvent () {
      console.log('starting listening the events:')
      this.historyList = []
      switch (this.type) {
        case undefined:
          break
        case 'roll':
          this.initRollEvent()
          break
      }
    },
    initRollEvent () {
      console.log('roll event:')
      // log bets + output to web3 for precise 'payout on win' field in UI
      // event LogBet(bytes32 indexed BetID, address indexed PlayerAddress, uint indexed RewardValue, uint ProfitValue, uint BetValue, uint PlayerNumber, uint RandomQueryID)

      // output to web3 UI on bet result
      // Status: 0=lose, 1=win, 2=win + failed send, 3=refund, 4=refund + failed send*/
      // event LogResult(uint indexed ResultSerialNumber, bytes32 indexed BetID, address indexed PlayerAddress, uint PlayerNumber, uint DiceResult, uint Value, int Status, bytes Proof);

      // log manual refunds
      // event LogRefund(bytes32 indexed BetID, address indexed PlayerAddress, uint indexed RefundValue);

      // log owner transfers
      // event LogOwnerTransfer(address indexed SentToAddress, uint indexed AmountTransferred);
      let options = {
        fromBlock: 2973072,
        toBlock: 'latest',
        filter: {}
      }
      if (this.onlyMe) {
        options.filter.PlayerAddress = this.myAddr
      }
      // console.log(options)
      this.contract.events.LogBet(options).on('data', event => {
        // console.log('LogBet', event)
        this.safeUnshift(this.dealEventData(event))
      })
      this.contract.events.LogResult(options).on('data', event => {
        // console.log('LogResult', event)
        this.updateListendingData(event)
      })
      this.contract.getPastEvents('LogBet', options).then(events => {
        console.log('PastBet:', JSON.parse(JSON.stringify(events)))
        events.map(item => {
          return this.dealEventData(item)
        })
        this.historyList = events.reverse()
        this.contract.getPastEvents('LogResult', options).then(result => {
          result.forEach(item => {
            this.updateListendingData(item)
          })
          // console.log(this.historyList)
        })
      })
    },
    /**
     * [dealEventData 处理事件的数据]
     * @param  {[type]} item [description]
     * @return {[type]}      [description]
     */
    dealEventData (item) {
      if (this.type === 'roll') {
        if (item.event === 'LogBet') {
          // console.log(item.returnValues.BetID)
          item.returnValues.Status = ''
          item.returnValues.DiceResult = ''
          item.returnValues.PlayerAddress = item.returnValues.PlayerAddress.substr(0, 10)
          item.returnValues.BetValue = Number(item.returnValues.BetValue) / Math.pow(10, 18)
          item.returnValues.ProfitValue = Number(parseFloat((item.returnValues.ProfitValue) / Math.pow(10, 18)).toFixed(4))
          item.returnValues.RewardValue = Number(parseFloat((item.returnValues.RewardValue) / Math.pow(10, 18)).toFixed(4))
          // if (item.returnValues.BetID === '0x9d3fefa2b7a60685bc6d33a1f23b7ea7a5dab92e3b289ad711f0b9efa6670265') {
          //   debugger
          // }
          if (item.returnValues.ProfitValue === 0) {
            item.returnValues.ProfitValue = '-'
          }
          if (item.returnValues.RewardValue === 0) {
            item.returnValues.RewardValue = '-'
          }
        }
      }
      item.active = false
      return item
    },
    /**
     * [updateListendingData 根据result的数据进行更新bet的数据]
     * @param  {[type]} item [description]
     * @return {[type]}      [description]
     */
    updateListendingData (item) {
      if (this.type === 'roll') {
        for (var i in this.historyList) {
          let oitem = this.historyList[i]
          if (oitem.returnValues.BetID === item.returnValues.BetID) {
            oitem.returnValues.DiceResult = item.returnValues.DiceResult
            let status = item.returnValues.Status
            oitem.returnValues.Status = status
            let datas = {
              hash: oitem.transactionHash,
              item: {
                status: status === '0' ? 2 : status === '1' ? 1 : 0
              }
            }
            this.changePending(datas)
          }
        }
      }
    },
    openRecord (item) {
      if (item.active === true) {
        item.active = false
      } else {
        for (var i in this.historyList) {
          this.historyList[i].active = false
        }
        item.active = true
      }
    },
    getMyEvent () {
      this.onlyMe = !this.onlyMe
      this.initEvent()
    },
    safeUnshift (item) {
      let flag = false
      for (var i in this.historyList) {
        if (this.historyList[i].transactionHash === item.transactionHash) {
          flag = true
          break
        }
      }
      if (!flag) {
        this.historyList.unshift(item)
      }
    }
  },
  watch: {
    contract: {
      handler (nc, oc) {
        if (typeof nc === 'object' && nc._provider.connected) {
          this.initEvent()
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@keyframes spin {
  0% {
    transform rotate(0deg)
  }
  100% {
    transform rotate(360deg)
  }
}
.ranklist
  max-width 450px
  height calc(100vh - 4rem)
  overflow-y auto
  margin 0 auto
  background #364362
  padding 0px 10px 20px 10px
  .tabbar
    display flex
    width 100%
    .bar
      flex 1
      height 0.8rem
      line-height 0.8rem
      text-align center
      color #303F69
      background #fff
      font-size 1.2em
      font-weight bolder
      border 1px solid #ccc
      cursor pointer
      &:nth-child(n+2)
        border-left none
    .active
      background #303F69
      color #fff
  .ranktitle
    width 100%
    height 0.6rem
    line-height 0.6rem
    display flex
    margin-bottom 0.05rem
    color #fff
    letter-spacing 0.02rem
    .titletxt
      flex 3
      text-align left
      font-size 1.6em
    .justme
      flex 2
      text-align center
      a
        color #fff
        display inline-block
        background transform
        height 0.4rem
        vertical-align middle
        line-height 0.4rem
      .active
        background rgba(255,255, 255, 0.3)
        border-radius 5px
        padding 0px 10px
  .ranklistcontent
    max-height calc(100vh - 3rem)
    ul
      li
        list-style none
        width 100%
        text-align center
        display block
        position relative
        &:nth-child(n+2)
          cursor pointer
          &:hover
            background rgb(93,108,149)
        .ttitle
          display flex
          font-weight bolder
          height 0.4rem
          line-height 0.4rem
          img
            width 0.3rem
            vertical-align middle
          .player
            flex 2
            text-align left
            display flex
            .avater,.addr
              flex 1
              text-align left
            .addr
              flex 2
              text-overflow ellipsis
              white-space nowrap
          .bets,.type,.res,.winning,.jackpot
            flex 1
            height 0.4rem
            line-height 0.4rem
          .type
            text-align left
          .arrow
            height 0.4rem
            line-height 0.4rem
            button
              height 0px
              background none
              border 1px solid #aeaeae
              border-width 0 3px 3px 0
              display inline-block
              padding 3px
              transform rotateZ(-45deg)
              vertical-align middle
              transition all ease-in-out 300ms
              &:focus
                outline none
            .active
              transform rotateZ(45deg)
        .tcontent
          width 100%
      .loading
        padding-top 1rem
        img
          text-align center
          width 20%
          animation spin 1.2s infinite
      .title
        border-bottom 1px solid #ccc
        padding-right 6px
        .ttitle
          &:after
            content ''
</style>



