/**
 * @authors liming.liu (liming.liu@71bird.com)
 * @date    2018-09-10 20:09:50
 */
<template>
  <div class="txcontent">
    <ul>
      <li>
        <div class="keys">地址</div>
        <div class="values" v-text="tx.address"></div>
      </li>
      <li>
        <div class="keys">赌注</div>
        <div class="values" v-text="tx.betValue"></div>
      </li>
      <li>
        <div class="keys">将赌注押在</div>
        <div class="values" v-text="tx.betOn"></div>
      </li>
      <li>
        <div class="keys">交易地址</div>
        <div class="values">
          <a :href="`https://etherscan.io/tx/${tx.txhash}`" target="_blank" v-text="tx.txhash.substr(0,30)"></a>
        </div>
      </li>
      <li>
        <div class="keys">开奖结果</div>
        <div class="values" v-text="tx.betOut"></div>
      </li>
      <li>
        <div class="keys">jackpot</div>
        <div class="values" v-text="tx.jackpot"></div>
      </li>
      <li>
        <div class="keys">获胜</div>
        <div class="values" v-text="tx.win"></div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'TxContent',
  data () {
    return {
      tx: {
        address: '',
        betValue: '',
        betOn: '',
        txhash: '',
        betOut: '',
        jackpot: 0,
        win: 0
      }
    }
  },
  props: ['content', 'type'],
  created () {
    if (this.type === 'roll') {
      this.tx.address = this.content.returnValues.PlayerAddress
      this.tx.betValue = this.content.returnValues.BetValue
      this.tx.betOn = this.content.returnValues.PlayerNumber
      this.tx.txhash = this.content.transactionHash
      this.tx.betOut = this.content.returnValues.DiceResult
      // this.tx.jackpot = this.content.returnValues.
      this.tx.win = this.content.returnValues.ProfitValue
    }
  }
}
</script>

<style lang="stylus" scoped>
.txcontent
  width 100%
  padding 10px
  background #2E3952
  ul
    li
      display flex
      list-style none
      text-align left
      font-size 0.6em
      .keys
        flex 1
        color #8C8E92
      .values
        flex 2
        color #fff
        text-overflow ellipsis
        white-space nowrap
        a
          color #fff
</style>



