import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import GameLobby from '@/components/GameLobby'
import Single from '@/components/Single/Single'

import Etheroll from '@/components/Single/Etheroll'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/gamelobby',
      name: 'GameLobby',
      component: GameLobby
    },
    {
      path: '/single',
      name: 'Single',
      component: Single
    },
    {
      path: '/single/etheroll',
      name: 'Etheroll',
      component: Etheroll
    }
  ]
})



