<template>
  <div id="app">
    <Layout class="layoutcontent">
      <Header class="header">
        <v-header></v-header>
      </Header>
      <Content class="content scroller">
        <router-view/>
      </Content>
      <Footer class="footer">
        <v-footer></v-footer>
      </Footer>
    </Layout>
    <div class="loadingwp" v-if="loading">
      <div class="pending">
        <div class="pendingtxt">
          Please Check your Metamask...
        </div>
        <div class="pendingimg">
          <img src="/static/images/pubpending.svg" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VHeader from '@/components/Common/Header'
import VFooter from '@/components/Common/Footer'
import {mapGetters} from 'vuex'
export default {
  name: 'App',
  components: {
    VHeader,
    VFooter
  },
  computed: {
    ...mapGetters(['loading'])
  }
}
</script>

<style lang="stylus">
rrem(val){
  return (val/192px)rem
}
@keyframes spin {
  0% {
    transform rotate(0deg)
  }
  100% {
    transform rotate(360deg)
  }
}
#app
  box-sizing border-box
  transform translateZ(0)
  position relative
  z-index 1
  overflow-y hidden
  &:before
    content ' '
    display block
    position absolute
    width 100%
    height 100%
    left 0
    top 0
    z-index -1
    background-image linear-gradient(135deg, #222c49 0%, #506ab4 100%)
  &:after
    content ' '
    display block
    position absolute
    width 100%
    height 100%
    left 0
    top 0
    z-index -1
    background-image url(/static/images/bg.png)
    opacity 0.02
    background-size 10%
    transform rotateZ(0deg)
  .layoutcontent
    position relative
    z-index 1
    background transparent
    .header
      height 1rem
      background transparent
      @media screen and (max-width: 768px)
        padding 0px
    .header,.content,.footer
      @media screen and (min-width: 800px)
        width 100%
        min-width 800px
        max-width 1000px
        margin 0 auto
      @media screen and (min-width: 1200px)
        width 100%
        min-width 1100px
        max-width 1600px
      @media screen and (max-width: 768px)
        width 100%
    .content
      height calc(100vh - 3rem)
      overflow-y auto
      padding 0px 50px
      @media screen and (max-width: 768px)
        padding 0px
    .footer
      background transparent
      box-sizing border-box
      padding 0px 50px
      @media screen and (max-width: 768px)
        padding 0px
  .loadingwp
    position absolute
    left 0
    top 0
    height 100%
    width 100%
    background rgba(0,0,0,0.45)
    z-index 1000
    .pending
      position absolute
      width 300px
      height 80px
      left 50%
      top 50%
      transform translate(-50%, -50%)
      background rgba(0,0,0,0.5)
      border-radius 10px
      display flex
      text-align center
      .pendingtxt
        flex 2
        font-size 1.3em
        color rgba(255,255,255,0.65)
        padding 10px 5px
        line-height 30px
        vertical-align middle
      .pendingimg
        flex 1
        line-height 80px
        img
          width 50px
          vertical-align middle
          animation spin 800ms infinite
</style>



