<template>
  <div class="etheroll">
    <Row>
      <Col :md="{span: 6}" :xs="{span:12}" class="lucklywp">
        <div class="luckly">
          <div class="jackpot">
            <img src="/static/images/prizepool.svg" alt="">
            <br>
            <span>奖池： <span v-text="lucklyPoll"></span>ETH</span>
          </div>
          <div class="probability">
            <span class="color" :class="{bolder: rateFocus}" v-text="lucklyRate"></span>%的几率获得奖池的<span class="color">30</span>%
          </div>
          <div class="explain">
            投注每增加0.01即增加1%的中奖几率，最高5%
          </div>
        </div>
      </Col>
      <Col :md="{span: 10}" :xs="{span:12}" class="rollwp">
        <div class="roll">
          <div class="profit">
            <div class="gamebling">
              下注： <span v-text='betValue'></span>ETH
            </div>
            <div class="gain">
              预计收益： <span v-text='profit'></span>ETH
            </div>
          </div>
          <div class="stage">
            <div class="multiple">
              <div class="multxt">
                <span v-text='rateValue' :class="{bolder: rateFocus}"></span>%
              </div>
              <div class="stagetip">
                <span>投注倍数</span>
              </div>
            </div>
            <div class="rangwp">
              <input type="range" id="rangeBar" class="rangeBar" min="2" max="98" step="1" v-model="rateValue">
              <div class="scale">
                <div class="sitem">1%</div>
                <div class="sitem">25%</div>
                <div class="sitem">50%</div>
                <div class="sitem">75%</div>
                <div class="sitem">98%</div>
              </div>
              <div class="stagetip">
                <span>拖动滑块选择(1% - 98%)的胜率</span>
              </div>
            </div>
          </div>
          <div class="bet">
            <div class="quickly">
              <div class="qitem"><button @click="addBetValue(0.05)">0.05</button></div>
              <div class="qitem"><button @click="addBetValue(0.1)">0.1</button></div>
              <div class="qitem"><button @click="addBetValue(betMax)">最大</button></div>
            </div>
            <div class="betswp">
              <div class="betsdeal">
                <div class="sub"><span @click="addBetValue(-0.01)">-</span></div>
                <div class="betinput">
                  <input type="text" v-model.number="betValue" @keyup="checkBetValue">
                  <div class="gastips">
                    <span>Gas费用</span><span v-text="gasPrice"></span>
                  </div>
                </div>
                <div class="add">
                  <span @click="addBetValue(0.01)">+</span>
                </div>
              </div>
              <div class="betsaction">
                <button @click="doBets">下注</button>
              </div>
            </div>
          </div>
        </div>
      </Col>
      <Col :md="{span: 8}" :xs="{span:12}" class="historywp">
        <rank-list :contract="Roll" :type="'roll'" v-if="Roll"></rank-list>
      </Col>
    </Row>
    <Modal class="win" title="恭喜中奖" v-model="modalResult">
      <div class="content">
      </div>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import RankList from '../Common/RankList'
import config from '../../config'
import {mapActions} from 'vuex'
// import Promise from 'bluebird'
export default {
  data () {
    return {
      betValue: 0.10,
      betMin: 0.02,
      betMax: 0.5,
      rateValue: 96,
      rateFocus: false,
      lucklyRate: 1,
      contract: {
        contractBalance: 0,
        gamePaused: false,
        houseEdge: 990,
        houseEdgeDivisor: 1000,
        minBet: 0.1,
        payoutsPaused: false,
        totalBets: 362934
      },
      payloading: false,
      gasPrice: 0,
      GasPrice: 0,
      Roll: {},
      modalResult: false
    }
  },
  created () {
    let abi = JSON.parse(config.eth.abi)
    let temp = new this.$web3.eth.Contract(abi, config.eth.addr, {})
    // Promise.promisifyAll(temp.methods)
    this.Roll = temp
    if (typeof this.$mweb3.version === 'object') {
      this.mRoll = this.$mweb3.eth.contract(abi).at(config.eth.addr)
    } else {
      this.mRoll = temp
    }
    this.web3.eth.getGasPrice().then(res => {
      this.GasPrice = res
      console.log(res)
    })
    this.getContractData()
  },
  mounted () {
    let rangeBar = document.getElementById('rangeBar')
    rangeBar.addEventListener('mousedown', e => {
      this.rateFocus = true
    }, false)
    rangeBar.addEventListener('mouseup', e => {
      this.rateFocus = false
      this.estimateGas()
    }, false)
  },
  computed: {
    profit () {
      return parseFloat(((this.betValue * (100 - (this.rateValue - 1))) / (this.rateValue - 1) + this.betValue) * this.contract.houseEdge / this.contract.houseEdgeDivisor).toFixed(4)
    },
    lucklyPoll () {
      return Number(parseFloat(this.contract.contractBalance / Math.pow(10, 18)).toFixed(2))
    }
  },
  components: {
    RankList
  },
  methods: {
    ...mapActions(['UpdateHubLoading', 'pushPendingList']),
    /**
     * [addBetValue 对下注数量的增加]
     * @param {[number]} value [可以是正数或者负数]
     */
    addBetValue (value) {
      let tmp = Number(this.betValue) + Number(value)
      if (tmp > this.betMax) {
        tmp = this.betMax
      }
      if (tmp < this.betMin) {
        tmp = this.betMin
      }
      let luckly = parseInt((tmp - this.betMin) / 0.01)
      if (luckly > 5) {
        luckly = 5
      }
      if (luckly < 1) {
        luckly = 1
      }
      this.lucklyRate = luckly
      this.betValue = Number(parseFloat(tmp).toFixed(2))
      this.estimateGas()
    },
    /**
     * [checkBetValue 检测输入的下注数量]
     * @return {[type]} []
     */
    checkBetValue () {
      if (isNaN(this.betValue)) {
        this.betValue = this.betMin
      }
      if (this.betValue > this.betMax) {
        this.betValue = this.betMax
      }
      if (this.betValue < this.betMin) {
        this.betValue = this.betMin
      }
      this.addBetValue(0)
    },
    /**
     * [getContractData 在合约中获取基础信息]
     * @return {[type]} [description]
     */
    async getContractData () {
      let fetch = []
      let keys = Object.keys(this.contract)
      for (let i in this.contract) {
        fetch.push(await this.Roll.methods[i]().call())
      }
      for (let i in fetch) {
        if (!isNaN(fetch[i])) {
          fetch[i] = Number(fetch[i])
        }
        this.contract[keys[i]] = fetch[i]
      }
      // console.log(this.contract)
    },
    doBets () {
      // 1.检查环境（web3,loading）
      // 2.检查开售状态
      // 3.检查金额
      // 4.发起交易
      // 5.等待界面
      // 6.更新等待区数据
      if (this.payloading) {
        window.alertify.alert('交易正在进行中')
        return
      } else {
        this.payloading = true
      }
      if (typeof this.$mweb3.version === 'string') { // 没有metamask
        window.alertify.alert('Metamask未正确安装，请安装Metamask')
        return
      }

      if (this.contract.gamePaused) {
        window.alertify.alert('游戏处于暂停状态')
        return
      }
      let data = {
        value: this.toWei(this.betValue),
        gas: this.gasPrice
      }
      this.UpdateHubLoading()
      this.mRoll.playerRollDice(this.rateValue, data, (er, res) => {
        this.payloading = false
        this.UpdateHubLoading()
        if (er) {
          // window.alertify.error(er.message)
          console.error(er)
        } else {
          let pendingData = {
            hash: res,
            value: this.betValue,
            type: 'roll',
            status: 0, // 0等待，1.开奖中奖，2.开奖未中奖
            reward: 0
          }
          this.pushPendingList(pendingData)
          console.log(res)
        }
      })
    },
    estimateGas () {
      let data = {
        value: this.toWei(this.betValue)
      }
      if (this.$isMetamask) {
        this.mRoll.playerRollDice.estimateGas(this.rateValue, data, (err, result) => {
          if (!err) {
            this.gasPrice = result
          }
        })
      } else {
        console.log(this.mRoll)
        this.mRoll.methods.playerRollDice([this.rateValue]).estimateGas(data).then(gas => {
          console.log(gas)
          this.gasPrice = gas
        }).catch(e => {
          console.log(e)
        })
      }
      // this.Roll.methods.playerRollDice([this.rateValue]).call(data).then(res => {
      //   this.gasPrice = res
      // })
    },
    toWei (value) {
      if (this.$isMetamask) {
        return this.$mweb3.toWei('' + value, 'ether')
      } else {
        return this.$mweb3.utils.toWei('' + value, 'ether')
      }
    }
  }
}
</script>
<style lang="stylus">
lucklyHeight = 200px
rollHeight = 300px
.bolder
  font-size 1.3em
.etheroll
  height calc(100vh - 3rem)
  .lucklywp
    height calc(100vh - 3rem)
    box-sizing border-box
    position relative
    @media screen and (max-width: 768px)
      height 200px
    .luckly
      position absolute
      width 100%
      min-height lucklyHeight
      top 50%
      left 50%
      transform translate(-50%, -50%)
      display flex
      flex-direction column
      text-align center
      .jackpot
        flex 2
        line-height (lucklyHeight / 4)
        font-size 0.3rem
        font-weight bolder
        color #F3C379
        img
          display line-block
      .probability,.explain
        flex 1
        line-height (lucklyHeight / 4)
      .probability
        font-size 0.2rem
        font-weight bolder
        color #BAD785
      .explain
        font-size 0.1rem
        color #DBECDA
      .color
        color #E3593C
  .rollwp
    height calc(100vh - 3rem)
    box-sizing border-box
    position relative
    @media screen and (max-width: 768px)
      height 200px
    .roll
      position absolute
      width 100%
      min-height rollHeight
      top 50%
      left 50%
      transform translate(-50%, -50%)
      display flex
      flex-direction column
      text-align center
      .profit
        flex 2
        display flex
        flex-direction row
        text-align center
        color rgba(134,150,199,1)
        max-height (rollHeight / 9 * 2)
        .gamebling,.gain
          flex 1
          font-size 0.25rem
          height (rollHeight / 9 * 2)
      .stage
        flex 3
        display flex
        flex-direction row
        text-align center
        max-height (rollHeight / 3)
        .multiple,.rangwp
          font-size 0.2rem
          height (rollHeight / 3)
          line-height (rollHeight / 3)
          position relative
          .stagetip
            color #A6D091
            position absolute
            top 70%
            left 50%
            transform translate(-50%, 0)
            line-height (rollHeight / 9)
            font-size 0.8em
        .multiple
          flex 1
          .multxt
            color #fff
            font-weight bolder
            font-size 0.30rem
        .rangwp
          flex 4
          height 100%
          .rangeBar
            width 80%
            cursor pointer
          .scale
            position absolute
            width 80%
            left 10%
            top 10px
            line-height (rollHeight / 9)
            display flex
            .sitem
              flex 1
              text-align center
              color #fff
              &:first-child
                text-align left
              &:last-child
                text-align right
      .bet
        flex 4
        display flex
        flex-direction column
        text-align center
        .quickly
          padding 0px 30%
          margin-top (rollHeight / 9)
          flex 1
          display flex
          .qitem
            flex 1
            margin 0px 10px
            vertical-align bottom
            padding-top 15px
            box-sizing content-box
            button
              display inline-block
              width 100%
              height 30px
              border none
              border-radius 5px
              color #fff
              background #EA86A2
              cursor pointer
              &:focus
                outline 0
        .betswp
          flex 4
          display flex
          padding 0px 0% 0% 20%
          flex-direction row
          .betsdeal
            flex 4
            display flex
            text-align center
            padding 0px 10%
            .add,.sub
              width 60px
              color #fff
              line-height (rollHeight / 3)
              font-size 2em
              cursor pointer
              &:focus
                outline none
            .betinput
              flex 1
              line-height (rollHeight / 3)
              position relative
              input
                position absolute
                top 50%
                left 50%
                transform translate(-50%,-50%)
                height 50px
                width 100%
                border 1px solid #ccc
                border-radius 5px
                background transparent
                color #fff
                text-align center
                font-size 0.3rem
                font-weight bolder
              .gastips
                position absolute
                line-height 0.25rem
                color #DBECDA
                font-size 0.8em
                bottom 0
                width 100%
                text-align center
                overflow hidden
          .betsaction
            flex 2
            text-align center
            position relative
            button
              position absolute
              top 50%
              left 50%
              transform translate(-50%,-50%)
              width 90%
              border none
              border-radius 5px
              height 50px
              font-size 1.6em
              letter-spacing 0.1em
              font-weight bolder
              cursor pointer
              background #EFC079
              color #fff
              &:focus
                outline 0
.historywp
  padding-left 4%
  padding-top 50px
</style>



