<template>
  <div class="single">
    <div class="pagetitle">
      <h2>单人对局游戏中心</h2>
    </div>
    <div class="gamelist">
      <div class="gamewrapper">
        <div class="gameitem">
          <div class="gamelogo">
            <img src="/static/images/coin.svg" alt="">
          </div>
          <div class="gamedesc">
            <div class="gamename"></div>
            <div class="gameinfo"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'single',
  data () {
    return {}
  }
}
</script>

<style lang="stylus" scoped>
.single
  padding 1rem 0px
  .pagetitle,.gamelist
    width 100%
  .pagetitle
    text-align center
</style>



