/**
 * @authors liming.liu (liming.liu@71bird.com)
 * @date    2018-09-03 15:46:32
 */
<template>
  <div class="vheader">
    <Row class="headerinfo">
      <Col :md="6">
        <div class="accountinfo" v-if='showMyinfo'>
          <div class="avater">
            <img src="/static/images/avater.png" />
          </div>
          <div class="userinfo">
            <div class="walletaddr" v-text='address'></div>
            <div class="accountlevel">
              余额: <span v-text='balance'></span> ETH
            </div>
          </div>
          <div class="levelbox">
            <!-- <img src="/static/images/box.png" alt=""> -->
          </div>
        </div>
        <div v-else>
          &nbsp;
        </div>
      </Col>
      <Col :md="{span:6, offset: 10}">
        <div class="menulist">
          <ul>
            <li>
              <a href="javascript:;">
                游戏主页
              </a>
            </li>
            <li>
              <a href="javascript:;">F&Q</a>
            </li>
            <li>
              <a href="javascript:;">联系方式</a>
            </li>
          </ul>
        </div>
      </Col>
      <Col :md="{span:1}" class="pending">
        <div class="pendingtxt" @click="togglePending">
          <Badge type="info" :count="pendingCount" overflow-count="10">
            <img src="/static/images/pending.svg" alt="">
          </Badge>
        </div>
        <div class="pendinglist" :class="{active: pendingdrop}">
          <ul>
            <li v-for="(pl,index) in pendingList" :key="pl.hash" @click="readNotice(index)">
              <div class="type">
                <span v-if='pl.type==="roll"'>Etheroll</span>
              </div>
              <div class="bets" v-text="pl.value+'ETH'"></div>
              <div class="status">
                <span v-if ="pl.status===0">开奖中</span>
                <span v-if ="pl.status===1">恭喜中奖</span>
                <span v-if ="pl.status===2">错过大奖</span>
              </div>
              <div class="goto">
                <a href="javascript:;" v-if="pl.type==='roll'">查看</a>
                <!-- Goto visit celebrate of roll -->
              </div>
            </li>
            <li v-if="pendingList.length === 0">
              暂无挂起的交易
            </li>
          </ul>
        </div>
      </Col>
      <Col :md="{span:1}" class="langsetting">
        <div class="currentlang">
          <img :src="langImg" alt="" @click="toggleLangDrop">
          <div class="droplist" :class="{active: langdrop}">
            <ul>
              <li @click="toggleLangDrop('china')"><img src="/static/images/china.svg" alt=""> <span>简体中文</span></li>
              <li @click="toggleLangDrop('usa')"><img src="/static/images/usa.svg" alt=""> <span>Englisth</span></li>
            </ul>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
import util from '@/lib/util'
export default {
  name: 'vheader',
  data () {
    return {
      langimg: 'china',
      myAddr: '',
      balance: 0.00,
      showMyinfo: false
    }
  },
  created () {
    if (typeof this.$mweb3.version === 'object') {
      this.myAddr = this.$mweb3.eth.defaultAccount
      if (this.myAddr) {
        this.showMyinfo = true
        this.$web3.eth.getBalance(this.myAddr).then(res => {
          this.balance = Number(parseFloat(res / Math.pow(10, 18)).toFixed(4))
        })
      }
    }
  },
  computed: {
    ...mapGetters(['langdrop', 'pendingdrop', 'pendingList', 'pendingCount']),
    langImg: function () {
      return `/static/images/${this.langimg}.svg`
    },
    address () {
      return '地址:' + util.mixStr(this.myAddr)
    }
  },
  methods: {
    ...mapActions(['toggleLang', 'togglePending', 'delPendingList']),
    toggleLangDrop (lang) {
      if (typeof lang === 'string') {
        this.langimg = lang
      }
      this.toggleLang()
    },
    readNotice (index) {
      let item = JSON.parse(JSON.stringify(this.pendingList[index]))
      if (item.status > 0) {
        this.delPendingList(index)
        if (this.pendingList.length === 0) {
          this.togglePending()
        }
      }
      // TODO 跳转页面
      console.log(item)
    }
  }
}
</script>

<style lang="stylus" scoped>
.vheader
  height 1rem
  border-bottom 1px solid rgba(100,216,108, 0.45)
  padding 0.1rem
  .headerinfo
    height 0.8rem
    line-height 0.8rem
    display block
    .accountinfo
      height 0.8rem
      display flex
      .avater
        width 0.8rem
        margin-right 0.1rem
        img
          width 0.8rem
          height 0.8rem
      .userinfo
        flex 1
        height 0.8rem
        display flex
        color rgba(255,255,255, 0.6)
        flex-direction column
        .walletaddr,.accountlevel
          flex 1
          display block
          line-height 0.4rem
          overflow hidden
      .levelbox
        width 0.4rem
        text-align left
        line-height 0.8rem
        padding-top 0.2rem
        transition ease-in-out
        img
          width 0.4rem
          height 0.4rem
    .menulist
      width 100%
      height 0.8rem
      padding-top 0.1rem
      ul
        display flex
        height 0.6rem
        li
          flex 1
          list-style none
          height 0.6rem
          line-height 0.6rem
          text-align center
          position relative
          cursor pointer
          &:after
            content ''
            position absolute
            left 0
            top 100%
            height 2px
            width 0
            background #64D86C
            transition all ease-in-out 100ms
          &:hover
            a
              color #64D86C
            &:after
              width 100%
          a
            color #fff
            font-size 1.2em
            font-weight bolder
            display inline-block
          img
            height 0.6rem
    .pending
      text-align right
      line-height 1rem
      position relative
      .pendingtxt
        img
          width 25px
          height auto
          cursor pointer
      .pendinglist
        width 0px
        max-height 250px
        overflow-x hidden
        overflow-y auto
        box-sizing boder-box
        background rgba(21,26,39,0.3)
        border-radius 5px
        color #fff
        position absolute
        right 0px
        top 0.70rem
        z-index 1
        padding 0.1rem 0px
        transition all ease-in-out 300ms
        ul
          li
            padding 0px 0.1rem
            text-align left
            list-style none
            cursor pointer
            overflow hidden
            line-height 0.25rem
            height 0.25rem
            width 100%
            margin-bottom 0.05rem
            &:hover
              background rgba(21,26,39,0.45)
              color #64D86C
            .type,.bets,.status,.goto
              display inline
      .active
        width 200px
    .langsetting
      text-align right
      line-height 1rem
      .currentlang
        padding 0px
        position relative
        img
          width 25px
          height auto
          cursor pointer
        .droplist
          width 0px
          overflow hidden
          box-sizing boder-box
          background rgba(21,26,39,0.3)
          border-radius 5px
          color #fff
          position absolute
          right 0px
          top 0.70rem
          z-index 1
          padding 0.1rem 0px
          transition all ease-in-out 300ms
          ul
            li
              padding 0px 0.1rem
              text-align left
              list-style none
              cursor pointer
              overflow hidden
              &:hover
                background rgba(21,26,39,0.45)
              img
                width 20px
                display inline-block
                vertical-align middle
                margin-right 0.1rem
              height 0.35rem
              line-height 0.35rem
        .active
          width 100px
</style>



