export default {
  state: {
    langdrop: false, // 语言设置开关的状态
    pendingdrop: false, // 状态栏状态
    pendingList: [],
    audio: new Audio('/static/media/ding.wav'),
    loading: false // 全局加载状态
  },
  getters: {
    langdrop (state) {
      return state.langdrop
    },
    pendingdrop (state) {
      return state.pendingdrop
    },
    pendingList (state) {
      return state.pendingList
    },
    pendingCount (state) {
      return state.pendingList.length
    },
    loading (state) {
      return state.loading
    }
  },
  actions: {
    toggleLang (context) {
      context.commit('ToggleLang')
    },
    togglePending (context) {
      context.commit('TogglePending')
    },
    clearPendingList (context) {
      context.commit('ClearPendingList')
    },
    pushPendingList (context, item) {
      context.commit('PushPendingList', item)
    },
    delPendingList (context, index) {
      context.commit('DelPendingList', index)
    },
    changePending (context, options) {
      context.commit('UpdatePendingList', options)
    },
    UpdateHubLoading (context) {
      context.commit('UpdateHubLoading')
    }
  },
  mutations: {
    ToggleLang (state) {
      state.langdrop = !state.langdrop
      if (state.langdrop === true) {
        state.pendingdrop = false
      }
    },
    TogglePending (state) {
      state.pendingdrop = !state.pendingdrop
      if (state.pendingdrop === true) {
        state.langdrop = false
      }
    },
    ClearPendingList (state) {
      state.pendingList = []
    },
    PushPendingList (state, item) {
      state.pendingList.push(item)
    },
    DelPendingList (state, index) {
      state.pendingList.splice(index, 1)
    },
    UpdatePendingList (state, options) {
      for (var i in state.pendingList) {
        if (state.pendingList[i].hash === options.hash) {
          Object.assign(state.pendingList[i], options.item)
          state.audio.play() // 状态更改提示音
        }
      }
    },
    UpdateHubLoading (state) {
      state.loading = !state.loading
    }
  }
}



